import { _decorator, Button, Canvas, Component, director, Game, game, Node, tween, UITransform, Vec3 } from 'cc';
import { Plane } from './Plane';
import { GameCtrl } from './GameCtrl';
import { AudioToggle } from './AudioToggle';
import { Boss } from './Boss';
const { ccclass, property } = _decorator;

@ccclass('ButtonPlayScene')
export class ButtonPlayScene extends Component {
    @property(Button)
    public pauseBtn: Button = null;
    @property(Button)
    public homeBtn: Button = null;
    @property(Button)
    public restartBtn: Button = null;
    @property(Button)
    public resumeBtn: Button = null;
    @property(Node)
    public pauseNode: Node = null;
    @property
    public menuScene: string = "";
    @property
    public playScene: string = "";
    @property(Canvas)
    public canvas: Canvas = null;

    protected onLoad(): void {
        
    }

    Pause(){
        AudioToggle.instance.onAudioQueue(2);
        GameCtrl.instance.spawnEnabled = false;
        this.pauseNode.active = true;
        Plane.instance.spawnEnabled = false;
        Plane.instance.moveEnable = false;
        // Boss.instance.isMoving = false;
        Boss.instance.moveRandom.caller(tween().stop());
    }
    Home(){
        AudioToggle.instance.onAudioQueue(2);
        director.loadScene(this.menuScene);
    }
    Restart(){
        AudioToggle.instance.onAudioQueue(2);
        director.loadScene(this.playScene);
    }
    Resume(){
        AudioToggle.instance.onAudioQueue(2);
        GameCtrl.instance.spawnEnabled = true;
        Plane.instance.spawnEnabled = true;
        Plane.instance.moveEnable = true;
        this.pauseNode.active = false;
        // Boss.instance.isMoving = true;
    }
}


